
import Vue from 'vue';
import axios from 'axios';

import envConfig from './config';

import LoadingPlugin from 'vux/src/plugins/loading/index.js'
import ToastPlugin from 'vux/src/plugins/toast/index.js'

Vue.use(ToastPlugin);
Vue.use(LoadingPlugin);

axios.defaults.timeout = 60000;
axios.defaults.headers.post['Content-Type'] = 'application/json';



axios.interceptors.request.use(
config => {
if (!config.silent) {


Vue.$vux.loading.show({
text: '加载中'
});
}


if (config.method.toLowerCase() === 'post' || config.method.toLowerCase() === 'put') {

config.data.appcode = envConfig.appcode;
config.data.v = envConfig.version;
config.data.session = localStorage.getItem('certification') || '';


} else {

if (!config.hasOwnProperty('params')) {
if (config.hasOwnProperty('data')) {
config.params = config.data;
} else {
config.params = {};
}
}
config.params.appcode = envConfig.appcode;
config.params.v = envConfig.version;
config.params.session = localStorage.getItem('certification') || '';
}
return config;
},
error => {
Vue.$vux.toast.show({
text: '请求参数错误',
type: 'text'
});
return Promise.reject(error);
}
);


axios.interceptors.response.use(
res => {



Vue.$vux.loading.hide();
if (res.status == 200) {
if (!res.data) {
return {
status: 1,
msg: '服务器无数据响应'
};
}
return res.data;
} else {
return {
status: 1,
msg: '服务器异常'
};
}
},
error => {

Vue.$vux.loading.hide();
console.log(error);
console.log(error.response);
console.log(error.response.status);
switch (error.response.status) {
case 400:
Vue.$vux.toast.show({
text: '服务器异常',
type: 'text'
});
break;
case 401:
Vue.$vux.toast.show({
text: '服务器异常',
type: 'text'
});
break;
case 403:
Vue.$vux.toast.show({
text: '请求资源无权访问',
type: 'text'
});
break;
case 404:
Vue.$vux.toast.show({
text: '请求资源不存在',
type: 'text'
});
break;
case 500:
Vue.$vux.toast.show({
text: '服务器异常',
type: 'text'
});
break;
default:
Vue.$vux.toast.show({
text: '服务器异常',
type: 'text'
});
break;
}

return Promise.reject(error.response.data.msg || '服务器异常');
}
);

export default axios;



