

import Vue from 'vue';
import ToastPlugin from 'vux/src/plugins/toast/index.js'

Vue.use(ToastPlugin);

import axios from '@/utils/axios';
import api from '@/assets/api/reserve.api';

export default new Promise((resolve, reject) => {
resolve();
return;
axios({
method: 'get',
url: '/qiaodev/dev/query',
data: {
mobile: '18671451730',
smsNum: '111111'
}
}).then(response => {
if (response.status == 0) {
resolve(response);
} else {
Vue.$vux.toast.show({
text: response.msg,
type: 'text'
});
reject(response);
}
});
});



